            color: #ffffff;

        }
    }
} 

.bg-btn-gradient{
    background: linear-gradient(90deg, #00A2E9 0%, #004899 100%);
}

.bg-footer{
    background: #004899;
}

.footer-list-custom li a {
    color: #fff;
    padding: 7px 0;
    display: block;
    -webkit-transition: all 0.3s ease-in-out;
    transition: all 0.3s ease-in-out;
  }
  .footer-list-custom li a:hover {
    color: fff;
  }
  .footerseparator {
    margin: 0 10px;
    color: white;
  }

  .footer-links a {
    color: white;
    text-decoration: none;
    display: block;
    -webkit-transition: all 0.3s ease-in-out;
    transition: all 0.3s ease-in-out;
    align-items: right;
  }
  .footer-links span {
    margin: 0 8px;
  }
  .footer-social-link-custom .avatar-title {
    color: #ffffff;
    // background-color: rgba(255, 255, 255, 0.05);
    -webkit-transition: all 0.3s ease;
    transition: all 0.3s ease;
    font-size: medium;
  }
  .footer-social-link-custom .avatar-title:hover {
    color: #fff;
    background-color: var(--vz-primary);
  }

