.circle {
      width: 100px;
      height: 100px;
      border-radius: 50%;
      // background: #f9a825 ;
      display: flex;
      align-items: center;
      justify-content: center;
      font-size: 24px;
      font-weight: bold;
      color: #444;
      position: relative;
    }
    .yellow{
      background: #f9a825 ;
    }
    .red{
      background: #D6002A ;
    }
    .circle::before {
      content: '';
      position: absolute;
      width: 70%;
      height: 70%;
      background: #fff;
      border-radius: 50%;
    }

    .circle span {
      position: relative;
      z-index: 1;
    }

    .text {
      max-width: 250px;
      font-size: 14px;
      color: #333;
    }